import React, { useMemo, useCallback } from "react";
import { TextField, Label, Dropdown } from "monday-ui-react-core"
import BoardRelation from "./BoardRelation";

const ColumnField = ({ 
  field, 
  jobDetails, 
  monday,
  setJobDetails,
}) => {

  const changeFieldText = useCallback(value => {
    setJobDetails({
      ...jobDetails,
      [field.id]: {
        ...jobDetails[field.id],
        text: String(value),
      },
    })
  })

  return (
    <>
      {field.type === "text" && (
        <TextField
          id={field.id}
        />
      )}
      {field.type === "board-relation" && (
        <BoardRelation 
          field={field}
          monday={monday}
          
        />
      )}
      {field.type === "long-text" && (
        <textarea
          id={field.id}
        />
      )}
      {field.type === "numeric" && (
        <TextField 
          id={field.id}
        />
      )}
    </>
  )
}

export default ColumnField;